  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=30);
  opacity: 0.3;
}
.tips--inprocess {
  -moz-animation: inprocessAnimate 1.7s infinite cubic-bezier(0.4, 0, 1, 1);
  -webkit-animation: inprocessAnimate 1.7s infinite cubic-bezier(0.4, 0, 1, 1);
  animation: inprocessAnimate 1.7s infinite cubic-bezier(0.4, 0, 1, 1);
}
@-moz-keyframes inprocessAnimate {
  0%, 100% {
    opacity: 0.3;
  }
  50% {
    opacity: 1;
  }
}
@-webkit-keyframes inprocessAnimate {
  0%, 100% {
    opacity: 0.3;
  }
  50% {
    opacity: 1;
  }
}
@keyframes inprocessAnimate {
  0%, 100% {
    opacity: 0.3;
  }
  50% {
    opacity: 1;
  }
}
.tips--error {
  -moz-animation: tipsErrAnimate 0.5s ease-in-out;
  -webkit-animation: tipsErrAnimate 0.5s ease-in-out;
  animation: tipsErrAnimate 0.5s ease-in-out;
}
@-moz-keyframes tipsErrAnimate {
  0%, 100% {
    -moz-transform: translateX(0);
    transform: translateX(0);
  }
  12.5% {
    -moz-transform: translateX(-4px);
    transform: translateX(-4px);
  }
  37.5% {
    -moz-transform: translateX(4px);
    transform: translateX(4px);
  }
  62.5% {
    -moz-transform: translateX(-2px);
    transform: translateX(-2px);
  }
  87.5% {
    -moz-transform: translateX(2px);
    transform: translateX(2px);
  }
}
@-webkit-keyframes tipsErrAnimate {
  0%, 100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  12.5% {
    -webkit-transform: translateX(-4px);
    transform: translateX(-4px);
  }
  37.5% {
    -webkit-transform: translateX(4px);
    transform: translateX(4px);
  }
  62.5% {
    -webkit-transform: translateX(-2px);
    transform: translateX(-2px);
  }
  87.5% {
    -webkit-transform: translateX(2px);
    transform: translateX(2px);
  }
}
@keyframes tipsErrAnimate {
  0%, 100% {
    -moz-transform: translateX(0);
    -ms-transform: translateX(0);
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  12.5% {
    -moz-transform: translateX(-4px);
    -ms-transform: translateX(-4px);
    -webkit-transform: translateX(-4px);
    transform: translateX(-4px);
  }
  37.5% {
    -moz-transform: translateX(4px);
    -ms-transform: translateX(4px);
    -webkit-transform: translateX(4px);
    transform: translateX(4px);
  }
  62.5% {
    -moz-transform: translateX(-2px);
    -ms-transform: translateX(-2px);
    -webkit-transform: translateX(-2px);
    transform: translateX(-2px);
  }
  87.5% {
    -moz-transform: translateX(2px);
    -ms-transform: translateX(2px);
    -webkit-transform: translateX(2px);
    transform: translateX(2px);
  }
}
